<template>
  <div>
    <Navbar @setSearch="searchValue = $event"/>
    <Notes :notes="filterNotes" @delNote="delNote" @openEdit="openEdit"/>
    <Model v-show="modelShow" :edit="edit" @close="close" @addNote="addNote" @newNote="newNote"/>
    <button v-show="!modelShow" class="model-btn" @click="modelShow = true">
      <img src="./assets/images/edit.svg" alt="">
    </button>
  </div>
</template>

<script>
import Navbar from "./components/Navbar.vue";
import Notes from "./components/Notes.vue";
import Model from './components/Model.vue'
export default {
  components: {
    Navbar,
    Notes,
    Model
  },
  mounted() {
    this.getNotes()
  },
  watch: {
    notes: {
      handler(){
        localStorage.notes = JSON.stringify(this.notes)
      },
      deep: true
    }
  },
  data() {
    return {
      edit: false,
      modelShow: false,
      notes: [],
      noteId: null,
      searchValue: null,
    };
  },
  methods: {

    openEdit(id){
      this.noteId = id
      this.edit = true
      this.modelShow = true
    },
    addNote(note){
      this.notes.push(note)
    },
    newNote(editNote){
      let index = this.notes.findIndex((note) => note.id == this.noteId)
      this.notes[index].title = editNote.title
      this.notes[index].text = editNote.text
    },
    getNotes(){
      let localNotes = localStorage.notes
      if(localNotes){
        this.notes = JSON.parse(localNotes)
      }
    },
    delNote(id){
      let index = this.notes.findIndex((note) => note.id == id)
      this.notes.splice(index, 1)
    },
    close(){
      this.modelShow = false
      setTimeout(() => {
        this.edit = false
      }, 200);
    }
  },
  computed:{
    filterNotes(){
      return this.searchValue ? this.notes.filter((note) => 
      note.title.toLowerCase().includes(this.searchValue.toLowerCase())) : this.notes
    }
  }
};
</script>