<template>
    <Transition name="model">
        <div class="model" @click="closeModel">
            <div class="model__block" @click.stop="">
                <h3 class="model__block-title">{{edit == true ? $t("editLemon")  : $t("addEdit")}} {{ $t('okay')}}</h3>
                <div class="model__block_inputs">
                    <label>
                        <span>Title</span>
                        <textarea placeholder="Title" v-model="title"></textarea>
                    </label>
                    <label>
                        <span>Content</span>
                        <textarea placeholder="Content" v-model="text"></textarea>
                    </label>
                </div>
                <div class="model__block_btns">
                    <button class="model__block_btn cls" @click="closeModel">{{ $t('cancelEdit')}}</button>
                    <button v-if="!edit" class="model__block_btn add" @click="addNote">{{ $t('addEdit')}}</button>
                    <button v-else class="model__block_btn add" @click="newNote">{{ $t('editPencil') }}</button>
                </div>
            </div>
        </div>
    </Transition>
</template>

<script>
import { v4 as uuidv4 } from 'uuid';
    export default {
        props:{
          edit: {typeof: Boolean}  
        },
        data(){
            return{
                title: '',
                text: ''
            }            
        },
        methods: {
            closeModel() {
                this.$emit('close')
            },
            newNote(){
               if(this.title && this.text != ''){
                    let newNote = {
                        title: this.title,
                        text: this.text,
                        date: new Date().toLocaleDateString()
                    }
                    this.$emit('newNote', newNote)
                    this.title = this.text = ''
                    this.closeModel()
               } 
            },
            addNote(){
                if(this.title && this.text != ''){
                    let note = {
                    id: uuidv4(),
                    title: this.title,
                    text: this.text,
                    date: new Date().toLocaleDateString()
                }
                this.$emit('addNote', note)
                this.title = this.text = ''
                this.closeModel()
                }
            }
        },
    }
</script>

<style lang="scss" scoped>

</style>