<template>
    <header class="header">
        <nav v-if="search == false" class="header__nav">
            <button @click="swichLang" class="header__nav-lang">
                {{ $i18n.locale }}
            </button>
            <h1 class="header__nav-title">
                {{ $t('notes') }}
            </h1>
            <button class="header__nav-search" @click="search = true">
                <img src="../assets/images/search.svg" alt="">
            </button>
        </nav>
        <nav v-else class="header__search">
            <button class="header__search-back" @click="search = false, input = ''">
                <img src="../assets/images/back.svg" alt="" />
            </button>
            <input class="header__search-input"
             type="text"
              :placeholder="$t('searchLupa')"
               v-model="input"
                autofocus
                @change="setSearch" 
                ::placeholder="$t('searchLupa')">
            <button class="header__search-clear" @click="input = ''">
                <img src="../assets/images/clear.svg" alt="" />
            </button>
        </nav>
    </header>
</template>

<script>
export default {
    data() {
        return {
            search: false,
            input: '',
        }
    },
    watch: {
        input(newValue) {
            this.$emit('setSearch', newValue)
        },


    },
    methods: {
        swichLang() {
            this.$i18n.locale == 'ru' ? this.$i18n.locale = 'uz' : this.$i18n.locale = 'ru'
            localStorage.lang = this.$i18n.locale
        },

    },

}
</script>