<template>
  <div class="note" v-for="note in notes">
    <h3 class="note-title">{{note.title}}</h3>
    <p class="note-date">{{note.date}}</p>
    <p class="note-text">{{note.text}}</p>
    <div class="note__btns">
      <button class="note__btn edit" @click="openEdit(note.id)">
        <img src="@/assets/images/edit.svg" alt="">
        <span> {{ $t('editPencil') }}</span>
      </button>
      <button class="note__btn del" @click="delNote(note.id)"> 
        <img src="@/assets/images/del.svg" alt="">
        <span>{{ $t('deleteBottle') }}</span>
      </button>
    </div>
  </div>
</template>

<script>
  export default {
    props: {
      notes: {typeof: Array}
    },
    methods: {
      delNote(id){
        this.$emit('delNote', id)
      },
      openEdit(id){
        this.$emit('openEdit', id)
      }
    },
  }
</script>

