<template>
  <div class="notes">
    <div class="container">
      <div class="notes__top">
        <h2 class="notes__top-title">{{ $t('allNotes')}}</h2>
        <button class="notes__top-btn" @click="list = !list">
          <img src="@/assets/images/row.svg" alt="">
          <span v-if="list == false">{{ $t('todoPlan') }}</span>
          <span v-else>{{ $t('Setka') }}</span> 
        </button>
      </div>
      <div class="notes__items" :class="list == true ? 'list' : 'grid' ">
        <Note :notes="notes" @delNote="delNote" @openEdit="openEdit"/>
      </div>
    </div>
  </div>
</template>

<script>
import Note from '@/components/Note.vue'
  export default {
    data(){
      return{
        list:false
      }
    },
    components: {
      Note
    },
    props: {
      notes: {typeof: Array}
    },
    methods: {
      delNote(id){
        this.$emit('delNote', id)
      },
      openEdit(id){
        this.$emit('openEdit', id)
      }
    },
  }
</script>

